<template>
  <div class="lp_page">
    <div class="lp_switch">
      <el-switch
        v-model="onlyIn"
        inactive-color="#BECBD2"
        active-color="#56C9C2"
      ></el-switch>
      只看已投
    </div>

    <div
      class="hot_lp_item"
      v-for="(value, index) in tokens"
      :key="index"
      v-show="value.invested || !onlyIn"
    >
      <div class="new"></div>
      <el-row type="flex" justify="space-between">
        <el-col :span="18" class="title"
          >{{ value.token | tokenName }} Mdex LP</el-col
        >
        <el-col :span="6" class="col_right">
          <div class="icon"></div>
        </el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">年化收益：</el-col>
        <el-col :span="12" class="col_right">
          216%
          <div class="icon icon_small"></div>
          <div class="icon icon_small"></div>
        </el-col>
      </el-row>

      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">总锁仓量：</el-col>
        <el-col :span="12" class="col_right">
          {{ value.total }}
        </el-col>
      </el-row>

      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">已存：</el-col>
        <el-col :span="12" class="col_right">{{ value.myPledge }}</el-col>
      </el-row>

      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">我的总收益：</el-col>
        <el-col :span="12" class="col_right">{{ value.myIncome }} HUSKY</el-col>
      </el-row>

      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">当前收益:</el-col>
        <el-col :span="12" class="col_right"
          >{{ value.canWithdraw }} HUSKY</el-col
        >
      </el-row>

      <el-row type="flex" :gutter="20" justify="space-between">
        <el-col>
          <div
            class="button button_border"
            @click="onWithDraw(index, value.myPledge)"
          >
            提币
          </div>
        </el-col>
        <el-col>
          <!-- <div class="button button_border" @click="deposit(index)">质押</div> -->
          <div
            class="button button_border"
            @click="showPop(index, value.token)"
          >
            存币
          </div>
        </el-col>
      </el-row>

      <div
        class="button button_fill"
        @click="approve(value.token, value.address)"
        v-if="value.isAllow"
      >
        授权 {{ value.token | tokenName }} Mdex LP
      </div>
      <div class="button button_fill" @click="onWithDraw(index, 0)">
        领取收益
      </div>

      <el-row type="flex" justify="space-between">
        <el-col :span="16" class="can_use_left"
          >{{ value.token | tokenName }} Mdex LP可用：</el-col
        >
        <el-col :span="8" class="can_use_right">{{ value.balance }}</el-col>
      </el-row>

      <el-row type="flex" justify="end">
        <el-col :span="24" class="to_get" @click="onToMdex"
          >获得HOT-HBTC Mdex LP >
        </el-col>
      </el-row>
    </div>
    <!--列表结束-->

    <el-dialog
      :visible="dialogVisible"
      :show-close="false"
      width="calc(100% - 50px)"
      custom-class="invite_model"
      :title="currentCoin | tokenTitle"
      center
    >
      <div class="input_box">
        <input type="number" v-model="amount" placeholder="存币数量" />
        <button class="max" @click="all">MAX</button>
      </div>
      <div class="balance">
        余额：{{ popBalance }} {{ currentCoin | tokenName }} Medex LP
      </div>

      <div class="dialog-footer">
        <button @click="hidePop">取 消</button>
        <button @click="deposit(index)" class="confirm">确 定</button>
      </div>

      <div class="tip">
        提示：请确保您的钱包有足够的HT，以确保Heco链上转账的完成。
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Net from "@/assets/net.js";
if (typeof window.ethereum != "undefined") {
  var mintContract = new web3.eth.Contract(Net.LPMintabi, Net.LPContact);
}
var tokenList = { 0: "HOT_HOG_LP", 1: "HUSD_HOG_LP" }; //正式环境修改

export default {
  data() {
    return {
      drawer: false,
      direction: "btt",
      amount: "",
      currentCoin: "",
      popBalance: "--",
      index: 0,
      tokens: [
        {
          pid: 0,
          token: "HOT_HOG_LP",
          total: "--",
          output: "--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested: true,
          address: Net.HOT_HOG_LP,
          isAllow: false,
          canWithdraw: "--",
        },
        {
          pid: 1,
          token: "HUSD_HOG_LP",
          total: "--",
          output: "--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested: true,
          address: Net.HUSD_HOG_LP,
          isAllow: false,
          canWithdraw: "--",
        },
      ],
      onlyIn: false,
      showPool: true,
      address: "",
      dialogVisible: false,
    };
  },
  mounted() {
    var _this = this;
    if (typeof window.ethereum != "undefined") {
      _this.$getAddress();
      setTimeout(function () {
        _this.getData();
      }, 1000);
    }
  },
  filters: {
    tokenName: (value) => (value == "HOT_HOG_LP" ? "HOT-HOG" : "HUSD-HOG"),
    tokenTitle: (value) =>
      value == "HOT_HOG_LP" ? "存币 HOT-HOG" : "存币 HUSD-HOG",
  },
  methods: {
    onToMdex() {},
    getData() {
      var _this = this;
      for (let index in tokenList) {
        _this.isAllowance(tokenList[index], index);
        _this.getUserInfo(index);
        _this.getBalance(tokenList[index], index);
        _this.totalPledge(tokenList[index], index);
        _this.lookCanWithdraw(index);
        _this.numByPerDay(tokenList[index], index);
      }
    },
    onWithDraw(index, amount) {
      //提币或撤回
      var _this = this;

      if (_this.tokens[index].myPledge == 0) {
        this.$message.warning("暂未参与！");
        return;
      } else {
        if (amount == 0 && _this.tokens[index].canWithdraw == 0) {
          this.$message.warning("没有可领取的");
          return;
        }
      }

      var value = new BigNumber(amount).multipliedBy(BigNumber(10).pow(18));
      console.log("index:" + index, "value:" + value);
      mintContract.methods
        .withdraw(index, value)
        .send({ from: _this.address })
        .then(
          (res) => {
            _this.getData();
            _this.$message.success("操作成功！");
          },
          (error) => {
            _this.$message.warning("操作失败!");
          }
        );
    },

    all() {
      this.amount = this.popBalance;
    },
    hidePop() {
      this.dialogVisible = false;
      this.amount = "";
      this.index = "";
      this.popBalance = "";
    },
    showPop(index, token) {
      var _this = this;
      this.dialogVisible = true;
      this.index = index;
      this.currentCoin = token;
      this.popBalance = _this.tokens[index].balance;

      console.log("index:" + this.index + "balance:" + this.popBalance);
    },
    deposit(index) {
      //质押 OK

      var _this = this;

      if (_this.amount == "" || _this.amount <= 0) {
        _this.$message.warning("数量必须大于0!");
        return;
      }
      if (_this.amount > _this.popBalance) {
        _this.$message.warning("余额不足!");
        return;
      }

      _this.dialogVisible = false;

      var value = new BigNumber(_this.amount).multipliedBy(
        BigNumber(10).pow(18)
      );

      console.log("index:" + index, "value:" + value);
      mintContract.methods
        .deposit(index, value)
        .send({ from: _this.address })
        .then(
          (res) => {
            _this.getData();
            _this.$message.success("存币成功！");
          },
          (error) => {
            _this.$message.warning("操作失败!");
          }
        );
    },
    approve(token, address) {
      //授权OK
      var _this = this;
      var currentToken = new web3.eth.Contract(Net.LPTokenabi, address);
      var value = new BigNumber(10000000).multipliedBy(BigNumber(10).pow(18));

      currentToken.methods
        .approve(Net.LPContact, value)
        .send({ from: _this.address })
        .then(
          function (res) {
            _this.getData();
            _this.$message.success("授权成功！");
          },
          (error) => {
            _this.$message.warning("操作失败!");
          }
        );
    },
    isAllowance(token, index) {
      //查询是否授权 OK
      var _this = this;
      var currentToken = new web3.eth.Contract(Net.LPTokenabi, Net[token]);
      console.log(_this.address, Net.LPContact);
      currentToken.methods
        .allowance(_this.address, Net.LPContact)
        .call({ from: _this.address })
        .then(function (res) {
          res > 0
            ? (_this.tokens[index].isAllow = false)
            : (_this.tokens[index].isAllow = true);
          console.log(token + "授权数量：" + res / 1e18);
        });
    },
    getUserInfo(index) {
      //我的质押和收益 OK
      var _this = this;
      mintContract.methods
        .userInfo(index, _this.address)
        .call({ from: _this.address })
        .then(function (res) {
          _this.tokens[index].myPledge = res.amount / 1e18;
          _this.tokens[index].myIncome = res.rewardDebt / 1e18;
          console.log(index + "池子的信息：" + JSON.stringify(res));
        });
    },
    getBalance(token, index) {
      //获取余额 OK
      var _this = this;
      var currentToken = new web3.eth.Contract(Net.LPTokenabi, Net[token]);

      currentToken.methods
        .balanceOf(_this.address)
        .call({ from: _this.address })
        .then(function (res) {
          _this.tokens[index].balance = res / 1e18;
          console.log(token + "余额：" + res / 1e18);
        });
    },
    totalPledge(token, index) {
      //获取总质押 OK
      var _this = this;
      console.log("总质押地址" + Net[token]);

      var currentToken = new web3.eth.Contract(Net.LPTokenabi, Net[token]);
      currentToken.methods
        .balanceOf(Net.LPContact)
        .call({ from: _this.address })
        .then(function (res) {
          _this.tokens[index].total = res / 1e18;
          console.log(token + "总质押：" + res / 1e18);
        });
    },
    lookCanWithdraw(index) {
      //查看可提取
      var _this = this;

      mintContract.methods
        .pendingHusky(index, _this.address)
        .call({ from: _this.address })
        .then(function (res) {
          _this.tokens[index].canWithdraw = res / 1e18;
          console.log(index + "池可提取：" + res / 1e18);
        });
    },

    numByPerDay(token, index) {
      //日产量
      var _this = this;
      mintContract.methods
        .numByPerDay(index)
        .call({ from: _this.address })
        .then(function (res) {
          _this.tokens[index].output = res / 1e18;
          console.log(index + "日产量：" + res / 1e18);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
</style>


